import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController } from '@ionic/angular';
import { createAnimation } from "@ionic/core";
import { Store } from '@ngrx/store';
import { TodoAdd } from 'src/app/store/actions/notification-action';
import { Notification } from 'src/app/store/models/notification';


@Component({
  selector: 'app-shopping',
  templateUrl: './shopping.page.html',
  styleUrls: ['./shopping.page.scss'],
})
export class ShoppingPage implements OnInit {

  shoppingStorage:any = [];
  productType:string;
  totalPrice;
  showTotalCard:boolean;
  showeEmptyTotalMessageCard:boolean;
  isBottomSheetOpened:boolean;
  historiqueStorage :any;
  commande:any = [];
  currentDate = new Date();

  constructor(  private router: Router,
                public alertController: AlertController,
                private store : Store<{notification: Notification[]}>
    ) { }

  openBottomSheet(){
    (<HTMLStyleElement>document.querySelector("#paymentBottomSheet")).style.display ="block";
    const animation = createAnimation()
      .addElement(document.querySelector("#paymentBottomSheet"))
      .easing("ease-in-out")
      .duration(400)
      .direction("alternate")
      .keyframes([
        { height: "0px", transform: "scale(1)", opacity: "1",},
        { height: "26rem", transform: "scale(1)", opacity: "1" },
      ]);
    animation.play();
  }

  closeBottomSheet(){
    const animation = createAnimation()
    .addElement(document.querySelector("#paymentBottomSheet"))
    .easing("ease-in-out")
    .duration(300)
    .direction("alternate")
    .keyframes([
      { height: "26rem",},
      { height:  "0px", opacity: "0", },
    ]);
    animation.play();
  }

  refreshPage(){
    let currentUrl = this.router.url;
    this.router.navigateByUrl('/', {skipLocationChange: true}).then(() => {
      this.router.navigate([currentUrl]);
    });
  }
    
  async paymentAlert(message) {
    const alert = await this.alertController.create({
      header: message,
      cssClass: 'shoppingAlertMessage',
      subHeader: '',
      buttons: [   
        {
          text: 'Ok',
          handler: (alertData) => {
            this.refreshPage()
          }
        },
      ]
    });
    await alert.present();
  }

  addNotification( ){
    let notification = {
      name:'',
      endDate:'',
      beginDate:'',
      message :'Paiement validé',
      service:'Restauration',
    }
    const newNotification = new Notification();
    newNotification.notification = notification;
    this.store.dispatch(new TodoAdd(newNotification));
    setTimeout(() => {
      (<HTMLStyleElement>document.querySelector(".alert-title")).style.fontSize="17px";
      (<HTMLStyleElement>document.querySelector(".alert-title")).style.fontWeight="normal";
    }, 100);
  }

  payment(){
    if(localStorage.getItem('restaurationHistorique')){
        this.historiqueStorage = JSON.parse(localStorage.getItem('restaurationHistorique'));
    }else{
        this.historiqueStorage = [];
    }
    this.commande.push(this.currentDate,this.totalPrice)
    this.historiqueStorage.push(this.commande)
    localStorage.setItem('restaurationHistorique',JSON.stringify(this.historiqueStorage))
    localStorage.removeItem('restaurationProduct');
    this.paymentAlert('Paiement réalisé avec succès');
    this.addNotification();
  }
      
  ngOnInit() {
    if(JSON.parse(localStorage.getItem('restaurationProduct'))){
      this.showTotalCard = true;
      this.showeEmptyTotalMessageCard = false;
      this.shoppingStorage = JSON.parse(localStorage.getItem('restaurationProduct'));
      this.commande.push(this.shoppingStorage)
      console.log(this.commande)
      console.log(this.shoppingStorage)
      this.productType = this.shoppingStorage [0].productType;
      let sum = 0;
      this.shoppingStorage.map(total=>{
        sum += Number(total.price)
        console.log(sum)
        this.totalPrice = sum.toFixed(2)
      })
    }else{
      this.showeEmptyTotalMessageCard = true;
      this.showTotalCard = false;
    }
  }
}
